body[data-mountain="earth"] {
  background-color: #313644;
}
body[data-mountain="earth"] .title-nav {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
body[data-mountain="earth"] .nav-earth {
    background: rgba(255, 255, 255, 0.1);
    pointer-events: none;
}
body[data-mountain="earth"] .logo {
    color: rgba(255, 255, 255, 0.9);
}
body[data-mountain="earth"] footer {
    color: rgba(255, 255, 255, 0.5);
}
body[data-mountain="earth"] .title {
    color: #fff !important;
}
body[data-mountain="earth"] a:link {
    color: #B3DEF1;
}
body[data-mountain="earth"] a:visited {
    color: #88cbe9;
}
body[data-mountain="earth"] a:hover {
    color: #def1f9;
}
body[data-mountain="earth"] .data {
    background-color: rgba(255, 255, 255, 0.7);
    cursor: pointer;
    max-height: none;
    overflow-y: visible;
    position: relative;
    -webkit-transition: 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    transition: 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
@media (min-width: 500px) {
body[data-mountain="earth"] .data {
        position: absolute;
}
}
body[data-mountain="earth"] .data:before {
      content: "view mountain...";
      color: rgba(255, 255, 255, 0.8);
      opacity: 0;
      -webkit-transition: opacity 0.25s;
      transition: opacity 0.25s;
      top: -30px;
      left: 20px;
      height: auto;
      width: auto;
}
body[data-mountain="earth"] .data:hover {
      background-color: rgba(255, 255, 255, 0.9);
}
body[data-mountain="earth"] .data:hover:before {
        opacity: 1;
}
body[data-mountain="earth"] .data-close {
    visibility: visible;
}
body[data-mountain="earth"] .about-block,
  body[data-mountain="earth"] .about-container h3 {
    color: rgba(255, 255, 255, 0.8);
}
body[data-mountain="earth"] #arrow-top,
  body[data-mountain="earth"] #arrow-bottom {
    fill: #fff;
    opacity: 0.3;
}
body[data-mountain="earth"] #arrow-bottom {
    opacity: 0.4;
}
